
label {
    color: white;
    margin-bottom: 0.5rem;
}

.form {
    background-color: rgba(0 , 0 , 0 , 0.5);
    max-height: 500px;
}

form {
    display: flex;
    flex-direction: column;
    padding: 4rem 1rem;
    margin: auto;
    max-width: 600px;
    transform: translateY(-30%)
}

form input, form textarea {
    margin-bottom: 1rem;
    padding: 10px 18px;
    font-size: 1.2rem;
    background-color: rgba(255 , 255 , 255 , 0.1);
    border-color: rgba(255 , 255 , 255 , 0.1);
    color: #f4f4f4;
}